.application-card {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    text-align: center;
  }
  
  .application-link {
    display: block;
    text-decoration: none;
    color: inherit;
  }
  
  .application-logo {
    width: 100%;
    height: auto;
    border-bottom: 1px solid #ddd;
  }
  
  .application-info {
    padding: 15px;
  }
  
  .application-name {
    font-size: 1.2rem;
    margin: 0;
  }
  