.applications-container {
    padding: 20px;
    background-color: #f9f9f9; /* Light background color */
  }
  
  .applications-header {
    font-size: 2rem;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .applications-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    padding: 0;
    list-style: none;
  }
  
  .application-card {
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    text-align: center;
  }
  
  .application-link {
    display: block;
    text-decoration: none;
    color: inherit;
  }
  
  .application-logo {
    width: 100%;
    height: auto;
    border-bottom: 1px solid #ddd;
  }
  
  .application-info {
    padding: 15px;
  }
  
  .application-name {
    font-size: 1.2rem;
    margin: 0;
  }
  